
@font-face {
  font-family: 'mt-app';
  src:  url('../../common/fonts/mt-app.eot?hbafk8');
  src:  url('../../common/fonts/mt-app.eot?hbafk8#iefix') format('embedded-opentype'),
    url('../../common/fonts/mt-app.ttf?hbafk8') format('truetype'),
    url('../../common/fonts/mt-app.woff?hbafk8') format('woff'),
    url('../../common/fonts/mt-app.svg?hbafk8#mt-app') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'mt-app' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-thumb_up:before {
  content: "\e900";
}
.icon-thumb_down:before {
  content: "\e901";
}
.icon-shopping_cart:before {
  content: "\e902";
}
.icon-remove_circle_outline:before {
  content: "\e903";
}
.icon-keyboard_arrow_right:before {
  content: "\e904";
}
.icon-favorite:before {
  content: "\e905";
}
.icon-close:before {
  content: "\e906";
}
.icon-check_circle:before {
  content: "\e907";
}
.icon-arrow_lift:before {
  content: "\e908";
}
.icon-add_circle:before {
  content: "\e909";
}
.cartcontrol {
    font-size: 0;
}
.cartcontrol .cart-decrease {
    display: inline-block;
    width: 26px;
    height: 26px;
    font-size: 26px;
    color: #b4b4b4;
}
.cartcontrol .cart-add .bg {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: black;
    position: absolute;
    left: 3px;
    top: 3px;
    z-index: -1;
}
.cartcontrol .cart-count {
    display: inline-block;
    width: 25px;
    text-align: center;
    font-size: 12px;
    line-height: 26px;
    vertical-align: top;
}
.cartcontrol .cart-add {
    display: inline-block;
    width: 26px;
    height: 26px;
    font-size: 26px;
    color: #ffd161;
    position: relative;
}
.move-enter-active,.move-leave-active{
	-webkit-transition: all 0.5s linear;
	transition: all 0.5s linear;
}
.move-enter,.move-leave-to{
	-webkit-transform: translateX(20px) rotate(180deg);
	        transform: translateX(20px) rotate(180deg);
}



