  z-index: 99;
}
.shopcart-wrapper .content-left[data-v-2aaff58c] {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  /* background: red; */
}

/* 右侧样式 */
.shopcart-wrapper .content-right[data-v-2aaff58c] {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 110px;
          flex: 0 0 110px;
