  color: #c4c4c4;
  line-height: 50px;
}
.shopcart-wrapper .content-left .desc-wrapper[data-v-2aaff58c] {
  float: left;
  margin-left: 19px;
}
.shopcart-wrapper .content-left .desc-wrapper .tip[data-v-2aaff58c] {
