}
.shopcart-wrapper .content-left .logo-wrapper .logo.highligh[data-v-2aaff58c] {
  color: #2d2b2a;
}
.shopcart-wrapper .content-left .desc-wrapper .tip.highligh[data-v-2aaff58c] {
