	vertical-align: middle;
}
.shopcart-wrapper .shopcart-list .list-content[data-v-2aaff58c]{
	max-height: 360px;
	overflow: hidden;
	background: white;
}
.shopcart-wrapper .shopcart-list .list-content .food-item[data-v-2aaff58c]{
	height: 38px;
	padding: 12px 12px 10px 12px;
	border-bottom: 1px solid #F4F4F4;
}
.shopcart-wrapper .shopcart-list .list-content .food-item .desc-wrapper[data-v-2aaff58c]{
	float: left;
	width: 240px;
}
.shopcart-wrapper .shopcart-list .list-content .food-item .desc-wrapper .desc-left[data-v-2aaff58c]{
	float: left;
	width: 170px;
}
.shopcart-wrapper .shopcart-list .list-content .food-item .desc-wrapper .desc-left .name[data-v-2aaff58c]{
	font-size: 16px;
	margin-bottom: 8px;
	
