	margin-top: 6px;
}
.shopcart .shopcart-mask[data-v-2aaff58c]{
	position: fixed;
	top: 0;
	right: 0;
	width: 100%;
	height: 100%;
	z-index: 98;
	background: rgba(7,17,27,0.6);
}



